@import '../utils/colors.scss';

* {
    padding: 0px;
    margin: 0px;
}

.cart {
    // display: none;
    // position: fixed;
    background-color: $White;
    z-index: 20;
    margin-right: -50px;
    top: 70px;
    right: 150px;
    width: 300px;
    border-radius: 10px;
    box-shadow: 0 10px 20px rgba($color: Black, $alpha: .2);
    .cart-header {
        padding: 20px;
        font-weight: bold;
        border-bottom: 2px solid $LightGrayishBlue;;
    }
    .cart-body {
        padding: 20px;
        height: 120px;
        display: flex;
        flex-direction: column;
            align-items: center;
            justify-content: center; 
        .cart-item {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;
            font-size: 15px;
            .cart-item-img {
                width: 50px;
                height: auto;
                border-radius: 5px;
            }
            .cart-item-decs {
                padding: 5px 0px;
                font-size: 14px;
                .totalPrice{
                font-weight: bold;
                }
            }
        }
        .checkout-btn {
                width: 100%;
                padding: 15px 0px;
                background-color: $Orange;
                border-radius: 10px;
                border: 0px;
                margin-top: 20px;
                color: $White;
                cursor: pointer;
            }
    }
}

@media (max-width: 425px) {
    .cart {
    top: 100px;
    width: 90%;
    right: 0;
    left: 0;
    margin: auto;
    }
}